    @use 'base/mixins' as m;
    @use 'base/variables' as v;

    .boletos {
        padding: 5rem 0;
        h3 {
            text-align: center;
            color: v.$blanco;
            margin-bottom: 2rem;
        }
        .contenedor-boletos {
                @include m.tablet {
                        @include m.grid(2, 5rem);
                }
        }
    }
    .pase {
        .nombre-pase {
            color: v.$blanco;
            text-align: center;
            font-size: 2.4rem;
            font-weight: 500;
        }
        &-header {
            background-color: v.$rosa;
            p {
                text-transform: uppercase;
                font-size: 1.6rem;
            }
        }
        &-header, &-footer {
            padding: .5rem;
            p {
                padding: .5rem;
                color: v.$blanco;
                text-align: center;
                margin: 0;
                font-weight: 700;
            }
        }
        &-body {
            background-color: v.$blanco;
            padding: .5rem;
            ul {
                @include m.resetear-lista;
                li, :nth-child(odd) {
                    background-color: v.$blanco;
                    padding: 1rem;
                    border-bottom: 1px solid #e1e1e1;
                }
            }
        }
        &-footer{
            background-color: v.$morado;
            p {
                font-size: 2.4rem;
            }
        }
    }
